import { Link } from "react-router-dom";
import { User } from "react-feather";

//Import own Components
import AuthForm       from "Components/AuthForm";
import FormInputGroup from "Components/Global/FormInputGroup";
import "./RecoverPasswordPage.scss";

const RecoverPasswordPage = ({
	delegations : {
		hookForm,
		handleRecoverSubmit,
	},
}) => {
	return (
		<div id="RecoverPasswordPage">
			<AuthForm
				Footer={Footer}
				labelBtn="Enviar Correo"
				title="Restablecer Contraseña"
				handleSumbitForm={handleRecoverSubmit}
				subTitle="Ingresa tu nombre de usuario. Haremos llegar un correo con las instrucciones para restablecer tu contraseña."
			>
				<FormInputGroup
					name="user"
					label="Nombre de Usuario"
					icon={<User  size={16} />}
					register={hookForm.register}
					errors={hookForm.formState.errors}
					placeholder="Ingresa el nombre de usuario"
				/>
			</AuthForm>
		</div>
	);
};

const Footer = () => (
	<>
		<p>Voler a </p>
		<Link to="/auth/login">Inicio Sesión.</Link>
	</>
);

export default RecoverPasswordPage;
