// Import Own Components
import {
	fetchClient,
	createServiceSlice,
} from "Helpers";
import AuthSlice from "Store/Slices/AuthSlice";

const AuthService = createServiceSlice({
	login : async ({ username, password }, { dispatch }) => {
		try {
			const body = new FormData();

			body.append("username", username);
			body.append("password", password);

			const response = await fetchClient("/auth/login", {
				method : "POST",
				body,
			});

			if (response.status !== 200) {
				return response.status;
			}

			const { user, token } = await response.json();

			dispatch(AuthSlice.actions.setUserData({ user, token }));

			return response.status;
		} catch (err) {
			console.error("[AuthService.login] ", err);
			return 500;
		}
	},
	recoverPassword : async ({ username }, { dispatch }) => {
		try {
			const body = new FormData();

			body.append("username", username);

			const response = await fetchClient("/auth/recover-password", {
				method : "POST",
				body,
			});

			if (response.ok) {
				const { data : email } = await response.json();

				dispatch(AuthSlice.actions.setEmail(email));
				return response.status;
			}
			return response.status;
		} catch (err) {
			console.error("[AuthService.forgotPassword] ", err);
		}
	},
	resetPassword : async ({ password, token }, { dispatch }) => {
		try {
			const body = new FormData();

			body.append("password", password);

			const response = await fetchClient("/auth/reset-password", {
				body,
				token,
				method : "POST",
			});

			if (response.status !== 200) {
				return response.status;
			}

			dispatch(AuthSlice.actions.clearUserData());

			return response.status;
		} catch (err) {
			console.error("[AuthService.resetPassword] ", err);
		}
	},
});

export default AuthService;
