import { connect }       from "react-redux";
import { useHistory }    from "react-router-dom";
import { useForm }       from "react-hook-form";
import { AlertTriangle } from "react-feather";

//Import own Components
import { bindAll }         from "Helpers";
import toaster             from "Components/Global/Toaster";
import AuthService         from "Store/Services/AuthService";
import RecoverPasswordPage from "./RecoverPasswordPage";

const RecoverPasswordPageContainer = ({ authService }) => {

	const history = useHistory();

	const { handleSubmit, ...hookForm } = useForm({
		defaultValues : {
			user : "",
		},
	});

	const handleRecoverSubmit = (extraProps) => handleSubmit(async data => {

		const {
			handleStartLoading   = () => {},
			handleFinishLoading  = () => {},
		} = extraProps;

		if (typeof(handleStartLoading) === "function") {
			handleStartLoading();
		}

		const status = await authService.recoverPassword({
			username : data.user,
			password : data.password,
		}).unwrap();

		if (typeof(handleStartLoading) === "function") {
			handleFinishLoading();
		}

		if (status === 200) {
			history.push("/auth/email_send");
			return;
		} else if (status === 404) {
			toaster.info({
				icon  : AlertTriangle,
				title : "El usuario no existe",
				body  : "Favor de verificar que el usuario sea correcto.",
			});
		} else {
			toaster.info({
				icon  : AlertTriangle,
				title : "Error",
				body  : "No se ha podido completar la operación, revisa tu conexión a internet.",
			});
		}
	})();

	return (
		<RecoverPasswordPage
			delegations={{
				hookForm,
				handleRecoverSubmit,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({ AuthService });

export default connect(null, mapDispatchToProps)(RecoverPasswordPageContainer);
